import { Button, Card, Zoom } from '@mui/material';
import { makeStyles } from '@mui/styles';
import { useContext } from 'react';
import { AppContext } from '../context/appContext';
import colores from '../styles/colors';
import sizeLetter from '../styles/fontSize';
import TypographyStyled from './typografyStyled';
import { isMobile } from '../styles/isMobile';
const FinalCard = ({ msjf }) => {
	const { reStart, handleMsjFinalCerebro } = useContext(AppContext);
	const classes = useStyle({ msjf, isMobile: isMobile() });

	return (
		<Zoom in={true}>
			<Card className={classes.resultScr}>
				<TypographyStyled
					style={{
						fontSize: isMobile()
							? sizeLetter.cellNormal
							: sizeLetter.normal,
						fontWeight: 'bold',
						color: colores.mainStrong,
					}}
				>
					{!msjf && handleMsjFinalCerebro().txt1}
				</TypographyStyled>
				<br />
				<TypographyStyled
					style={{
						fontSize: isMobile()
							? sizeLetter.cellNormal
							: sizeLetter.normal,
						color: colores.letterSecond,
					}}
				>
					{!msjf ? handleMsjFinalCerebro().txt2 : msjf}
				</TypographyStyled>
				<br />
				<Button
					variant='contained'
					color='secondary'
					onClick={() => reStart()}
				>
					<TypographyStyled
						style={{
							fontSize: isMobile()
								? sizeLetter.cellSmall
								: sizeLetter.small,
						}}
					>
						Jugar de nuevo
					</TypographyStyled>
				</Button>
			</Card>
		</Zoom>
	);
};

const useStyle = makeStyles((theme) => ({
	resultScr: {
		minHeight: 350,
		marginLeft: ({ msjf, isMobile }) =>
			!isMobile && (msjf !== undefined ? '18%' : '8%'),
		marginRight: ({ msjf, isMobile }) =>
			!isMobile && (msjf !== undefined ? '18%' : '8%'),
		backgroundColor: colores.backgSoft + '!important',
		borderRadius: '15px!important',
		borderBottom: `8px solid ${colores.secondPink}`,
		padding: 50,

		display: 'flex',
		textAlign: 'center',
		flexDirection: 'column',
		alignItems: 'center',
		justifyContent: 'center',
	},
}));

export default FinalCard;
