import React, { useContext } from "react";
import { Card, Tabs, Typography, Zoom, Grid, Fade } from "@mui/material";
import { makeStyles } from "@mui/styles";
import colores from "../../styles/colors";
import sizeLetter from "../../styles/fontSize";
import SpringTxt from "../../components/springTxt";
import { useSpring, animated } from "react-spring";
import { AppContext } from "../../context/appContext";
import FinalCard from "../../components/finalCard";
import { finalMsjGlosario } from "../../context/dataJuegoGlosario";

function TabPanel(props) {
  const { children, value, index, ...other } = props;
  const classes = useStyles();
  return (
    <div
      role="tabpanel"
      //hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      //style={{ display: "flex" }}
      {...other}
    >
      {value === index && (
        <Card elevation={3} className={classes.panelCard} p={3}>
          <Grid container spacion={3}>
            {children}
          </Grid>
        </Card>
      )}
    </div>
  );
}

export default function VerticalTabs() {
  const {
    activeStep,
    handlerModalOpenGlosario,
    score,
    openModal,
    dataGlosario,
  } = useContext(AppContext);
  const classes = useStyles();
  console.log("Game3");
  console.log(activeStep, score, openModal);

  const springRadar = useSpring({
    loop: true,
    to: [
      {
        opacity: 0,
        transform: "scale(6)",
        backgroundColor: "purple",
      },
    ],
    from: {
      opacity: 1,
      transform: "scale(1)",
      backgroundColor: "purple",
      height: 50,
      width: 50,
      borderRadius: "50%",
      position: "absolute",
      top: 25,
      left: 25,
      transition: "transform 2s",
      zIndex: -100,
    },
  });

  if (dataGlosario[activeStep] === undefined)
    return (
      <FinalCard
        msjf={
          score > 4 ? finalMsjGlosario.correcto : finalMsjGlosario.incorrecto
        }
      />
    );

  return (
    <Grid container spacing={2}>
      <Grid item sm={1} />
      <Grid item sm={2}>
        <Tabs
          orientation="vertical"
          value={activeStep}
          indicatorColor={"transparent"}
        >
          {dataGlosario.map((btn, i) => (
            <BtnStep i={i} btn={btn} btnProv={activeStep} key={btn.id} />
          ))}
        </Tabs>
      </Grid>
      <Grid item sm={8}>
        {dataGlosario.map((data, i) => (
          <TabPanel value={activeStep} index={i} key={data.title + i}>
            <Grid item xs={12}>
              <Fade in={!openModal} {...{ timeout: 800 }}>
                <Typography className={classes.panelTitle}>
                  {data.title}
                </Typography>
              </Fade>
            </Grid>
            <Grid item md={8}>
              <SpringTxt
                open={!openModal}
                size={sizeLetter.extraBig}
                txts="este es una de las consignas que se despliegan en la pantalla"
              />
            </Grid>

            <Grid item md={3}>
              {[
                "rgba(201, 8, 8,.6)",
                "rgba(230, 226, 28,.6)",
                "rgba(8, 201, 21,.6)",
              ].map((dat, i) => (
                <Fade
                  in={!openModal}
                  {...{ timeout: (i + 1) * 400 }}
                  key={dat + i}
                >
                  <Card
                    variant="contained"
                    className={classes.btnSemaforo}
                    style={{ backgroundColor: dat }}
                    onClick={() => handlerModalOpenGlosario(dat, data)}
                  >
                    <animated.div />
                  </Card>
                </Fade>
              ))}
            </Grid>
          </TabPanel>
        ))}
      </Grid>
      <Grid item sm={1} />
    </Grid>
  );
}

const BtnStep = ({ btn, btnProv, i }) => {
  const classes = useStyles({ btnProv, i, btn });

  return (
    <Zoom in={true} timeout={300 * i + 400}>
      <animated.div elevation={3} className={classes.btnStep}>
        {btn.id}
      </animated.div>
    </Zoom>
  );
};

