import { Grid, Button, Fade, Card } from "@mui/material";
import { makeStyles } from "@mui/styles";
import { useContext } from "react";
import { AppContext } from "../../context/appContext";
import cerebroPng from "../../assets/cerebro-04.svg";
import colores from "../../styles/colors";
import SpringTxt from "../../components/springTxt";
import sizeLetter from "../../styles/fontSize";
import FinalCard from "../../components/finalCard";
import { isMobile } from "../../styles/isMobile";
import "../../styles/shakeeff.css";

const Game2 = () => {
  const { dataJuegoDos, activeStep } = useContext(AppContext);
  const classes = useStyles();

  return (
    <Grid
      container
      spacing={10}
      style={{
        minHeight: "80vh",

        //marginTop: ".5%",
      }}
    >
      <Grid item md={5} xs={12} className={classes.centerContent}>
        <CardBrain />
      </Grid>
      <Grid item md={6} xs={12} className={classes.centerContent}>
        {dataJuegoDos[activeStep] === undefined ? <FinalCard /> : <CardResp />}
      </Grid>
    </Grid>
  );
};

const CardResp = () => {
  const { dataJuegoDos, score, handlerStep, activeStep, showEff, setShowEff } =
    useContext(AppContext);
  const classes = useStyles({ score, dataJuegoDos, activeStep });

  function handleSituation(btn) {
    setShowEff();
    setTimeout(() => {
      handlerStep(dataJuegoDos[activeStep].respuesta, btn);
    }, 900);
  }

  return (
    <Card className={classes.cardResp}>
      <div className={classes.SpringTxtRep}>
        <SpringTxt
          open={showEff}
          txts={dataJuegoDos[activeStep].pregunta}
          lspacing={0}
          whiteSpace={true}
          //color="#383838"
          size={isMobile() ? sizeLetter.cellExtraBig : sizeLetter.Big}
        />
      </div>
      <div style={{ height: "8vh" }}>
        <Button
          disabled={!showEff}
          color="secondary"
          variant="contained"
          className={classes.btn}
          onClick={(e) => handleSituation("true")}
        >
          SI
        </Button>
        <Button
          disabled={!showEff}
          color="secondary"
          variant="contained"
          className={classes.btn}
          onClick={(e) => handleSituation("false")}
        >
          No
        </Button>
      </div>
    </Card>
  );
};

const CardBrain = () => {
  const { dataJuegoDos, activeStep, score, showEff } = useContext(AppContext);
  const classes = useStyles({
    score,
    dataJuegoDos,
    activeStep,
    isMobile: isMobile(),
  });
  return (
    <div
      className={`${classes.brainContainer} ${
        showEff === false ? "shake" : ""
      }`}
    >
      <div className={classes.brainFondo}>
        <div className={classes.brainFondoColor} />
      </div>
      <div className={classes.brainImg} />
    </div>
  );
};

const useStyles = makeStyles(() => ({
  brainContainer: {
    position: "relative",
    height: ({ isMobile }) => (isMobile ? 240 : "100%"),
    width: "100%",
    transition: "all 0.5s ease",
  },
  brainFondoColor: {
    position: "absolute",
    height: "100%",
    width: "100%",
    transform: ({ score }) => `translateY(${100 - score * 10}%)`,
    transition: "all 0.5s ease",
    backgroundColor: colores.mainPink,
    /*  background: ({ score }) =>
      `linear-gradient(0deg, purple ${score * 10}%, #fff 0%)`, */
  },
  centerContent: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
  },
  brainFondo: {
    position: "absolute",
    // transform: "rotate(14deg)",
    marginTop: "8%",
    marginLeft: "12%",
    height: ({ isMobile }) => (isMobile ? 240 : "55vh"),
    width: "76%",
    borderRadius: "50%",
    overflow: "hidden",
    backgroundColor: "#fff",
  },
  brainImg: {
    position: "absolute",
    height: "100%",
    width: "100%",
    borderRadius: "50%",
    background: `url(${cerebroPng})`,
    backgroundSize: "100% 100%",
  },
  cardResp: {
    backgroundColor: [colores.mainSoft, "!important"],
    paddingBottom: 30,
    width: "100%",
    height: ({ isMobile }) => !isMobile && "60vh",
    borderRadius: [15, "!important"],
    display: "flex",
    textAlign: "center",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "space-between",
  },
  SpringTxtRep: {
    height: "48vh",

    display: "flex",
    textAlign: "center",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "space-between",
  },
  btn: {
    width: "8vw",
    height: "4vw",
    fontSize: ({ isMobile }) =>
      !isMobile
        ? [sizeLetter.big, "!important"]
        : [sizeLetter.cellBig, "!important"],
    marginRight: [35, "!important"],
    marginLeft: [35, "!important"],
    transition: ["transform .4s", "!important"],
    "&:hover": {
      transform: "scale(1.1)",
    },
  },
}));

export default Game2;
