import { makeStyles } from "@mui/styles";
import { Card, Zoom, Typography } from "@mui/material";
import { isMobile } from "../styles/isMobile";
import j1 from "../assets/fondoTarjeta.gif";
import sizeLetter from "../styles/fontSize";
import { AppContext } from "../context/appContext";
import { useContext } from "react";
import colores from "../styles/colors";

const GameCard = ({ cardData, i }) => {
  const { setRuta } = useContext(AppContext);
  const classes = useStyles();

  return (
    <Zoom in={true} timeout={180 * (i + 1)}>
      <Card
        elevation={5}
        className={classes.diagonalDiv}
        onClick={() => setRuta(cardData.link)}
      >
        <div id="imageDiv" className={classes.imageDiv}>
          <div id="hoverTxt" className={classes.hoverTxt}>
            <Typography className={classes.title}>{cardData.title}</Typography>
          </div>
        </div>
      </Card>
    </Zoom>
  );
};

const useStyles = makeStyles((theme) => ({
  diagonalDiv: {
    cursor: "pointer",
    border: "1px solid #fff",
    width: isMobile() ? "80%" : "38vh",
    height: isMobile() ? "14vh" : "58vh",
    margin: "auto",
    marginBottom: isMobile() && 30,
    transform: "skewX(-6deg)",
  },
  imageDiv: {
    height: "100%",
    width: "100%",
    position: "relative",
    background: `url(${j1})`,
    backgroundSize: "cover",
    backgroundPosition: "center center",
    display: "table",
    transition: ".3s",
    transitionTimingFunction: "ease",
  },
  hoverTxt: {
    height: "100%",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",

    color: colores.mainStrong,
    transition: ".3s",
  },
  title: {
    paddingLeft: "4%",
    paddingRight: "4%",
    fontFamily: "Poppins",

    fontSize: isMobile()
      ? [sizeLetter.cellBig, "!important"]
      : [sizeLetter.big, "!important"],
  },
  txt: {
    fontFamily: "Poppins",
    fontSize: [sizeLetter.small, "!important"],
    transform: "translateY(2000%)",
    transition: ".2s",
    transitionDelay: ".1s",
    transitionTimingFunction: "ease",
  },
}));

export default GameCard;
